<template>
  <div class="wrapper" @click="goFullscreen">
    <h1>{{printTitle}}</h1>
    <div class="list">
      <ul class="yet" >
        <li
          class="fade_in"
          v-for="(name, index) in printList"
          :key="name"
          :style="{ animationDelay: `${index * 0.2}s` }"
        >
          {{ name }}
        </li>
      </ul>
      <div class="yets" v-if="printYet.length > 0">
        <h2>Êú™ÊèêÂá∫ËÄÖ</h2>
        <ul class="yet">
          <li
            class="fade_in"
            v-for="(name, index) in printYet"
            :key="name"
            :style="{ animationDelay: `${index * 0.2}s` }"
          >
            {{ name }}
          </li>
        </ul>
      </div>
    </div>
  </div>
  <!-- <div class="slideshow">
    <h1>{{ slides[currentIndex] }}</h1>
  </div> -->
</template>

<script>
import { toRaw } from "vue";

export default {
  props: [
    "interval",
    "submissions", "students", "submissionStatus"
  ],
  data() {
    return {
      currentIndex: 0,
      date: '',
      submissionCount: 0, // ÊèêÂá∫Áâ©Êï∞
      localSubmissions: this.submissions, // ÊïôÁßë
      localStudents: this.students, // ÁîüÂæí
      localStatus: this.submissionStatus, // ÊèêÂá∫Áä∂Ê≥Å
      statusArr: [], // ÂèñÂæó„Åó„ÅüÈÖçÂàó„Çí‰øùÊåÅ
      printTitle: '', // Ë°®Á§∫Áî®„Çø„Ç§„Éà„É´
      printList: [], // Ë°®Á§∫„Åô„Çã„É™„Çπ„Éà
      printYet: [] // Êú™ÊèêÂá∫ËÄÖ„É™„Çπ„Éà
    }
  },
  created() {
    this.date = new Date().toLocaleDateString("ja-JP", {year: "numeric",month: "2-digit",day: "2-digit"}).replaceAll('/', '-'); // Êó•‰ªò„ÅÆÂèñÂæó
  },
  mounted() {
    console.log(this.date);
    // this.showSlide();
    // setInterval(this.showSlide, this.interval || 3000);
    setInterval(this.showStatus, this.interval || 3000);
    // ‰ªÆ
    this.showStatus(); // Ë°®Á§∫
    // this.setAnimationDelay(); // „Ç¢„Éã„É°„Éº„Ç∑„Éß„É≥
  },
  methods: {
    // showSlide() {
    //   this.currentIndex = (this.currentIndex + 1) % this.slides.length;
    // },
    goFullscreen() {
      document.documentElement.requestFullscreen();
    },
    showStatus() { // ÊèêÂá∫Áä∂Ê≥Å„Çí„Çπ„É©„Ç§„ÉâË°®Á§∫
      // currentIndex=0 „Åô„Åê„Å´Êõ¥Êñ∞
      if( this.submissionCount == 0 ) {
        this.updateStatus();
      }

      // Ë°®Á§∫
      console.log(this.submissionCount);
      if( this.submissionCount < 1 ) { // ÊèêÂá∫ÁÑ°„Åó
        return;
      }
      const status = toRaw(this.statusArr);
      console.log(status[this.currentIndex]);

      // „Çø„Ç§„Éà„É´Êõ¥Êñ∞
      this.printTitle = this.localSubmissions[status[this.currentIndex][0]];

      // ÂêçÂâç„É™„Çπ„ÉàÊõ¥Êñ∞
      let doneList = [];
      status[this.currentIndex][1].forEach(id => {
        doneList.push( this.localStudents[id] );
      });
      this.printList = doneList;

      // Êú™ÊèêÂá∫ËÄÖ„É™„Çπ„Éà
      let fullList = []; // ÂÖ®Âì°ÂêçÁ∞ø
      const names = toRaw(this.localStudents);
      Object.values(names).forEach(name => {
        fullList.push(name);
      });
      this.printYet = fullList.filter(item => !doneList.includes(item));

      // console.log(this.printYet);

      // currentIndex++
      this.currentIndex++;
      if( this.submissionCount == this.currentIndex) { // ÊúÄÂæå
        this.updateStatus();
        this.currentIndex = 0; // ÂàùÊúüÂåñ
      }

    },
    updateStatus() { // ÊèêÂá∫Áä∂Ê≥Å„ÇíÊõ¥Êñ∞
      console.log('Êõ¥Êñ∞');
      // ‰ªäÊó•„ÅÆÊèêÂá∫„Éá„Éº„Çø„Å™„Åó
      if( !this.localStatus[this.date] ) {
        this.printList.push('ÊèêÂá∫Áâ©„ÇíÁôªÈå≤„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ');
        this.printTitle = 'ÊèêÂá∫Êú™ÁôªÈå≤';
        return;
      }

      // ÊèêÂá∫Áâ©„ÅÆÁ®ÆÈ°û„Çí„Ç´„Ç¶„É≥„Éà
      const status = toRaw(this.localStatus[this.date]);
      // console.log(status);
      // console.log(Object.keys(status).length);
      this.submissionCount = Object.keys(status).length;
      
      // ÂÄ§„Å†„Åë„ÇíÈÖçÂàó„Å´Â§âÊèõ
      this.statusArr = Object.entries(status);
      // values.forEach((key, val) => {
      //   console.log(key, val);
      // });
    }
    // setAnimationDelay() {
    //   const lis = document.querySelectorAll('.fade_in');
    //   lis.forEach((li, i) => {
    //     li.style.animationDelay = `${0.2 * (i + 1)}s`;
    //   });
    // }
  }
};
</script>

<style scoped>
.wrapper {
  margin: 100px auto 0 auto;
}

h1 {
    position: absolute;
    top: 0;
    left: 0;
    margin-left: 1em;
    font-size: 3vw;
    z-index: 1;
}

.list {
  margin: 0 20px;
  padding: 20px;
  box-sizing: border-box;
  background-color: white;
  z-index: 2;
}
.list ul.yet {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  gap: 20px;
  margin: 0;
  padding: 20px;
}
.list ul.yet li.fade_in {
  opacity: 0;
  transform: translateY(10px);
  animation: fadeInUp 0.5s forwards;
  margin: 0.3em 0.7em; /* „ÅäÂ•Ω„Åø„ÅßÈñìÈöî */
  font-size: 3vw;
}
@keyframes fadeInUp {
  /* from {
    opacity: 0;
    transform: translateY(10px);
  } */
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.yets {
  display: flex;
  flex-wrap: wrap;  
  margin-top: 20px;
  padding: 10px;
  border: #ffc107 5px solid;
  background-color: #fad871;
  border-radius: 10px;
}
.yets h2 {
  margin: 0;
  font-size: 2vw;
  line-height: 1.0;
  color: #dc3545;
}
.yets ul.yet {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  gap: 10px;
  margin: 0;
  padding: 20px;
}
.yets ul.yet li.fade_in {
  opacity: 0;
  transform: translateY(10px);
  animation: fadeInUp 0.5s forwards;
  margin: 0.3em 0.7em; /* „ÅäÂ•Ω„Åø„ÅßÈñìÈöî */
  font-size: 2vw;
  line-height: 1.0;
}


.slideshow {
  width: 100vw;
  height: 100vh;
  background: black;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 5vw;
}
</style>
