<template>
  <div class="wrapper">
    <h1>Ë®≠ÂÆö</h1>
    <div class="form">
      <p class="alert">
        „ÅØ„Åò„ÇÅ„Å´„ÄÅ <a href="https://docs.google.com/spreadsheets/d/1Dpo6MONqsIqU4Lo0PWyDwym41Uq9RTqR/edit?usp=sharing&amp;ouid=115812607673416459316&amp;rtpof=true&amp;sd=true" target="_blank" rel="noopener noreferrer">„Çπ„Éó„É¨„ÉÉ„Éâ„Ç∑„Éº„Éà</a> „Çí„ÅîËá™Ë∫´„ÅÆ„Éâ„É©„Ç§„Éñ„Å∏„Ç≥„Éî„Éº„ÅÆ‰øùÂ≠ò„Åó„Å¶„ÄÅ„Éê„Éº„Ç≥„Éº„Éâ„ÇíÁîüÊàê„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ
      </p>
      <div class="box">
        <h2>ÊèêÂá∫Áâ©</h2>
        <div class="table">
          <table v-show="!submissionsSetting">
            <tr>
              <th>ID</th>
              <th>ÊèêÂá∫Áâ©Âêç</th>
            </tr>
            <tr v-for="(value, key) in localSubmissions" :key="key">
              <td>{{key}}</td>
              <td>{{value}}</td>
            </tr>
          </table>
          <button v-show="!submissionsSetting" v-on:click="submissionsSetting=!submissionsSetting">Á∑®ÈõÜ</button>

          <div class="edit" v-show="submissionsSetting">
            <p>„Çπ„Éó„É¨„ÉÉ„Éâ„Ç∑„Éº„Éà„Åã„ÇâÁõ¥Êé•Ë≤º„Çä‰ªò„Åë„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ</p>
            <textarea name="" id="" v-model="submissionsText"></textarea>
            <button @click="saveSubmissions">‰øùÂ≠ò</button>
          </div>

        </div>
      </div>
      <div class="box">
        <h2>ÁîüÂæí</h2>
        <div class="table">
          <table v-show="!studentsSetting">
            <tr>
              <th>ID</th>
              <th>ÂêçÂâç</th>
            </tr>
            <tr v-for="(value, key) in localStudents" :key="key">
              <td>{{key}}</td>
              <td>{{value}}</td>
            </tr>
          </table>
          <button v-show="!studentsSetting" v-on:click="studentsSetting=!studentsSetting">Á∑®ÈõÜ</button>

          <div class="edit" v-show="studentsSetting">
            <p>„Çπ„Éó„É¨„ÉÉ„Éâ„Ç∑„Éº„Éà„Åã„ÇâÁõ¥Êé•Ë≤º„Çä‰ªò„Åë„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ</p>
            <textarea name="" id="" v-model="studentsText"></textarea>
            <button @click="saveStudents">‰øùÂ≠ò</button>
          </div>
        </div>
      </div>
      <div class="box">
        <h2>ÊèêÂá∫Áä∂Ê≥Å„ÅÆ„Éá„Éº„Çø</h2>
        <div class="table">
          aaaa
        </div>
      </div>
      <div class="box">
        <h2>„Åù„ÅÆ‰ªñ</h2>
        <div class="table">
          <label>Ë°®Á§∫ÈñìÈöî(Áßí): 
            <input type="number" v-model.number="localInterval" min="1" />
          </label>
          <label>„Çπ„É©„Ç§„ÉâÂÜÖÂÆπÔºàÊîπË°åÂå∫Âàá„ÇäÔºâ:
            <textarea v-model="localSlides"></textarea>
          </label>
          <button @click="saveSettings">‰øùÂ≠ò</button>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  props: [
    "slides", "interval",
    "submissions", "students"
  ],
  data() {
    return {
      localSlides: this.slides.join("\n"),
      localInterval: this.interval / 1000,
      localSubmissions: this.submissions,
      localStudents: this.students,
      submissionsSetting: false, // ÊèêÂá∫Áâ©Á∑®ÈõÜ„ÅÆË°®Á§∫„Éï„É©„Ç∞
      studentsSetting: false, // ÁîüÂæíÁ∑®ÈõÜ„ÅÆË°®Á§∫„Éï„É©„Ç∞
      submissionsText: '', // ÊèêÂá∫Áâ©„ÅÆtextarea
      studentsText: '' // ÁîüÂæí„ÅÆtextarea
    };
  },
  methods: {
    saveSettings() {
      this.$emit("update-settings", {
        slides: this.localSlides.split("\n").filter(s => s.trim() !== ""),
        interval: this.localInterval * 1000
      });
    },
    saveSubmissions() { // ÊèêÂá∫Áâ©„Éá„Éº„Çø„ÅÆ‰øùÂ≠ò
      let submittions = this.submissionsText.split('\n');
      let ret = {}; // ËøîÂç¥Áî®ÈÖçÂàó
      for(let i = 0; i < submittions.length; i++) {
        if( submittions[i] == '' ) break; // Á©∫ÁôΩË°å„ÅßÁµÇ‰∫Ü
        
        let arr = submittions[i].split('\t');
        ret[arr[0]] = arr[1]; // ret[ID]=ÊèêÂá∫Áâ©Âêç

      }
      console.log(ret);
      this.$emit("save-submissions", ret );
      this.localSubmissions = ret; // data Êõ¥Êñ∞
      this.submissionsSetting = false; // Á∑®ÈõÜÁµÇ‰∫Ü
    },
    saveStudents() { // ÁîüÂæí„Éá„Éº„Çø„ÅÆ‰øùÂ≠ò
      let students = this.studentsText.split('\n');
      let ret = {}; // ËøîÂç¥Áî®ÈÖçÂàó
      for(let i = 0; i < students.length; i++) {
        if( students[i] == '' ) break; // Á©∫ÁôΩË°å„ÅßÁµÇ‰∫Ü
        
        let arr = students[i].split('\t');
        console.log(arr);
        ret[arr[1]*1] = arr[0]; // ret[ID]=Ê∞èÂêç

      }
      console.log(ret);
      this.$emit("save-students", ret );
      this.localStudents = ret; // dataÊõ¥Êñ∞
      this.studentsSetting = false; //Á∑®ÈõÜÁµÇ‰∫Ü
    }

  }
};
</script>

