<template>
  <div class="wrapper">
    <h1>ÊèêÂá∫Áâ©„ÇíÁôªÈå≤</h1>
    <div class="form">
      <ul class="radio-a">
        <li>
          <input name="type" id="is-option-a" type="radio" value="true" v-model="submitFlg">
          <label for="is-option-a">ÊèêÂá∫</label>
        </li>
        <li>
          <input name="type" id="is-option-b" type="radio" value="false" v-model="submitFlg">
          <label for="is-option-b">ÂèñÊ∂à</label>
        </li>
      </ul>
      <div class="barcode">
        <input type="text" ref="barcode" v-model="inputText" @keydown.enter.prevent>
      </div>
    </div>
  </div>

  <div class="toast" v-if="errorFlg && toasted">
    „Ç®„É©„Éº::<br>{{errorMsg}}&nbsp;<span>/</span>
  </div>
  <div class="toast" v-else-if="toasted">
    {{this.localStudents[this.studentsID]}}„Åï„Çì&nbsp;<span>/</span>&nbsp;{{this.localSubmissions[this.submissionsID]}}&nbsp;„Çí<span v-if="submitFlg">ÊèêÂá∫</span><span v-else>Âèñ„ÇäÊ∂à</span>„Åó„Åæ„Åó„Åü„ÄÇ
  </div>

</template>

<script>

export default {
  props: [
    "submissions", "students", "submissionStatus"
  ],
  emits: [
    'save-status'
  ],
  data() {
    return {
      date: "",
      errorFlg: false,
      errorMsg: '',
      submitFlg: true, // ÈÄÅ‰ø°„Éï„É©„Ç∞
      inputText: "",
      toasted: false,
      localSubmissions: this.submissions,
      localStudents: this.students,
      submissionsID: 0,
      studentsID: 0,
      localStatus: this.submissionStatus
    }
  },
  methods: {
    saveStatus() { // localStatus„Çí‰∏äÊõ∏„Åç
      // console.log('Form:save');
      // console.log(this.localStatus);
      this.$emit("save-status", this.localStatus );
    },
    error(msg) {
      this.errorMsg = msg;
      this.inputText = ''; // input„ÇíÁ©∫„Å´Êàª„Åô
      this.errorFlg = true;
      this.toasted = true; //„É°„ÉÉ„Çª„Éº„Ç∏Ë°®Á§∫
      setTimeout(() => { // ÔºíÁßíÂæå„Å´„Éà„Éº„Çπ„Éà„ÇíÊ∂à„Åô
        this.errorFlg = false;
        this.toasted = false;
      }, 2000);

    }
  },
  created() {
    this.date = new Date().toLocaleDateString("ja-JP", {year: "numeric",month: "2-digit",day: "2-digit"}).replaceAll('/', '-'); // Êó•‰ªò„ÅÆÂèñÂæó
    this.submitFlg = true; // ÊèêÂá∫„Å´„ÉÅ„Çß„ÉÉ„ÇØ
  },
  mounted() { // ÊèèÁîªÊôÇ„ÅÆÂá¶ÁêÜ
    this.$refs.barcode.focus(); // „Éê„Éº„Ç≥„Éº„Éâ„Å´„Éï„Ç©„Éº„Ç´„Çπ
  },
  watch: {
    inputText(e) { // inputText„ÇíÁõ£Ë¶ñ
      if( isNaN(e) ) { //Êï∞Â≠ó‰ª•Â§ñ„ÅåÂÖ•Âäõ„Åï„Çå„Åü->ÂâäÈô§
        console.log(e+':notKazu');
        this.inputText = this.inputText.slice(0, -1);
        this.error('„Éê„Éº„Ç≥„Éº„Éâ„ÇíË™≠„ÅøÂèñ„Çã„Åã„ÄÅÊèêÂá∫Áâ©„ÅÆÁï™Âè∑„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ');
        return false;
      }
      if( e.length == 7 ) { // 7 ÊñáÂ≠ó„Ç≥„Éº„ÉâÂèñÂæóÂÆå‰∫Ü
        // „Ç®„É©„Éº„Éï„É©„Ç∞
        let err = 0;

        // „Éê„Éº„Ç≥„Éº„Éâ„Åã„ÇâÊèêÂá∫Áâ©ID„Å®ÁîüÂæíID„ÇíË®àÁÆó
        this.submissionsID = this.inputText % 1000;
        this.studentsID = this.inputText - this.submissionsID;

        // ÁîüÂæíID„ÅåÂ≠òÂú®„Åô„Çã„Åã„ÉÅ„Çß„ÉÉ„ÇØ
        if( this.studentsID in this.localStudents === false ) {
          console.log('„Åù„ÅÆÁîüÂæíID„ÅØÂ≠òÂú®„Åó„Åæ„Åõ„Çì„ÄÇ[stuId]');
          this.error('„Åù„ÅÆÁîüÂæíID„ÅØÂ≠òÂú®„Åó„Åæ„Åõ„Çì„ÄÇ[stuId]');
          err++;
        }
        // ÊèêÂá∫Áâ©ID„ÅåÂ≠òÂú®„Åô„Çã„Åã„ÉÅ„Çß„ÉÉ„ÇØ
        if( this.submissionsID in this.localSubmissions === false ) {
          console.log('„Åù„ÅÆÊèêÂá∫Áâ©„ÅØÂ≠òÂú®„Åó„Åæ„Åõ„Çì„ÄÇ[subId]');
          this.error('„Åù„ÅÆÊèêÂá∫Áâ©„ÅØÂ≠òÂú®„Åó„Åæ„Åõ„Çì„ÄÇ[subId]');
          err++;
        }


        console.log(this.submissionsID, this.studentsID);
        console.log(this.localSubmissions[this.submissionsID]);
        console.log(this.localStudents[this.studentsID]);

        // Âèñ„ÇäÊ∂à„ÅóÂá¶ÁêÜ
        if(this.submitFlg == 'false') {
          this.submitFlg = false;
          console.log(this.submitFlg);

          // „Éç„Çπ„Éà„ÅåÂ≠òÂú®„Åô„Çã„Åã„Çí„ÉÅ„Çß„ÉÉ„ÇØ
          if(
            !this.localStatus[this.date] ||
            !this.localStatus[this.date][this.submissionsID]
          ) {
            console.log('„Åæ„Å†ÊèêÂá∫„Åï„Çå„Å¶„ÅÑ„Åæ„Åõ„Çì„ÄÇ[date,id]');
            this.error('„Åæ„Å†ÊèêÂá∫„Åï„Çå„Å¶„ÅÑ„Åæ„Åõ„Çì„ÄÇ[date,id]');
            err++;
          }

          // ÂÄ§„ÅÆ‰ΩçÁΩÆ„ÇíÊ§úÁ¥¢
          const index = this.localStatus[this.date][this.submissionsID].indexOf(this.studentsID);
          if( index === -1 ) { // „Éí„ÉÉ„Éà„Å™„Åó
            console.log('„Åæ„Å†ÊèêÂá∫„Åï„Çå„Å¶„ÅÑ„Åæ„Åõ„Çì„ÄÇ[stuId]');
            this.error('„Åæ„Å†ÊèêÂá∫„Åï„Çå„Å¶„ÅÑ„Åæ„Åõ„Çì„ÄÇ[stuId]');
            err++;
          }

          // „Ç®„É©„Éº„ÅÆ„Å™„ÅÑÊôÇÂàá„ÇäÂá∫„Åó
          if( err === 0 ) {
            this.localStatus[this.date][this.submissionsID].splice(index, 1); // Âàá„ÇäÂá∫„Åó
            this.saveStatus(); // ‰øùÂ≠ò
          }
        }
        // ÊèêÂá∫Âá¶ÁêÜ
        else {
          this.submitFlg = true;
          
          // „Ç®„É©„ÉºÁÑ°„Åó„ÅÆÊôÇÂÆüË°å
          if(err === 0) {
            // Êó•‰ªò„Åå„Å™„Åë„Çå„Å∞‰Ωú„Çã
            if(!this.localStatus[this.date]) {
              this.localStatus[this.date] = {};
              console.log("Êó•‰ªò‰ΩúÊàê");
            }
            // ÊèêÂá∫Áâ©„Åå„Å™„Åë„Çå„Å∞‰Ωú„Çã
            if(!this.localStatus[this.date][this.submissionsID]) {
              this.localStatus[this.date][this.submissionsID] = [];
            }
            // ÊèêÂá∫->ÈÖçÂàó„Å´ËøΩÂä†
            this.localStatus[this.date][this.submissionsID].push(this.studentsID);

            // ÈáçË§á„ÇíÁúÅ„Åè
            this.localStatus[this.date][this.submissionsID] = [...new Set(this.localStatus[this.date][this.submissionsID])]

            // ‰øùÂ≠ò
            this.saveStatus();

          }

        }

        this.toasted = true; //„É°„ÉÉ„Çª„Éº„Ç∏Ë°®Á§∫
        this.inputText = ''; // input„ÇíÁ©∫„Å´Êàª„Åô
        setTimeout(() => { // ÔºíÁßíÂæå„Å´„Éà„Éº„Çπ„Éà„ÇíÊ∂à„Åô
          this.toasted = false;
        }, 2000);
      }

    }

  }
};

</script>

<style scoped>
.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

h1 {
    position: absolute;
    top: 0;
    left: 0;
    margin-left: 1em;
    font-size: 3vw;
    z-index: 1;
}

.form {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background-color: white;
  z-index: 2;
}

.radio-a{
  display: flex;
  justify-content: center;
  margin-block: 20px;
  gap: 10px;
  padding: 0;
}
.radio-a li {
  list-style: none;
}
.radio-a input[type="radio"]{
  position: absolute;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  overflow: hidden;
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
}
.radio-a label{
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  background-color: #ddd;
  border: 3px solid #ddd;
  color: #fff;
  aspect-ratio: 1 / 1;
  width: 80px;
  cursor: pointer;
  transition: .3s;
}
.radio-a input[type="radio"]:checked + label{
  background-color: #006699;
  border-color: #006699;
  cursor: default;
}
@media (hover: hover){
  .radio-a li:hover label{
    border-color: #006699;
  }
}

.barcode {
  display: flex;
  margin-left: 20px;
  width: 400px;
  height: 80px;
  box-sizing: border-box;
  border-bottom: #006699 1px solid;
}
.barcode input[type="text"] {
  border: none;
  outline: none;
  align-items: baseline;
  width: 400px;
  font-size: 2vw;
  font-weight: bold;
  color: #006699;
  text-align: center;
}

div.toast {
    position: fixed;
    right: 30px;
    opacity: 0;
    font-family: Arial, Helvetica, sans-serif;
    color: white;
    font-weight: bold;
    padding: 15px 30px;
    border-radius: 10px 10px;
    background-color: rgb(28, 28, 28);
    animation-duration: 2s;
    animation-delay: 0s;
    animation-name: fadein;
    animation-fill-mode: forwards;
    box-shadow: 5px 5px 50px rgb(176, 176, 176);
}
div.toast span {
    color:rgb(61, 194, 0);
    padding-left: 5px;
    font-weight: bolder;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}
@keyframes fadein {
    0% {
        opacity: 0;
        bottom: 0;
    }
    15% {
        opacity: 1;
        bottom: 20;
    }
    95% {
        opacity: 1;
        bottom: 20;
    }
    100% {
        opacity: 0;
        bottom: 0;
    }
}
</style>